___CSS_LOADER_EXPORT___.push([module.id, `@media (max-width: 62.5em) {
  .detail-text h1 {
    font-size: 24px;
  }

  .detail-text h3 {
    font-size: 25px;
  }

  .detail-text h4 {
    font-size: 24px;
  }
}

@media (max-width: 47em) {
  .menu {
    display: block;
    z-index: 100;
  }

  .navbar {
    width: 100%;
    height: 50%;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    flex-direction: column;
    transform: translateX(100%);
    background-color: #fff;
    z-index: 50;
    transition: all 0.3s ease-in-out;
  }

  .open {
    transform: translateX(0);
    visibility: visible;
  }

  .menus {
    flex-direction: column;
  }

  .menus div {
    margin-bottom: 1rem;
  }
}

@media (max-width: 30em) {
  .container h1 a {
    font-size: 32px;
  }

  .detail-text h1 {
    font-size: 20px;
  }

  .detail-text h3 {
    font-size: 16px;
  }

  .detail-text h4 {
    font-size: 16px;
  }
}

.hero-element {
  background-image: url("/images/heros/hero-image_2-small.jpg");
}

@media screen and (min-width: 768px) {
  .hero-element {
    background-image: url("/images/heros/hero-image_2-large.jpg");
  }
}

@media screen and (min-width: 768px) {
  .hero {
    background-image: url('/images/heros/hero-image_2-large.jpg');
  }
}
