  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  font-size: 12px;
  color: #424242;
}

a{
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
}

.topnav {
  overflow: hidden;
  background-color: #333;
}

.topnav a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  min-width: 44px;
  min-height: 44px;
  text-decoration: none;
  font-size: 17px;
}

.topnav button {
  min-width: 44px;
  min-height: 44px;
  display: inline-block;
  display: block;
  color: #000000;
  text-align: center;
  padding: 14px 16px;
  margin-top: 5px;
  text-decoration: none;
  font-size: 14px;
}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

.topnav a.active {
  background-color: #04aa6d;
  color: white;
}

.topnav .icon {
  display: none;
}

@media screen and (max-width: 600px) {
  .topnav {
    flex-direction: column;
    align-items: flex-start;
  }

  .topnav a:not(:first-child) {
    display: none;
  }

  .topnav button.tggl {
    display: block;
    margin: 1rem;
  }

  .topnav.responsive {
    flex-direction: column;
  }

  .topnav.responsive a {
    display: block;
    text-align: left;
    min-width: 44px;
    min-height: 44px;
  }
}

