.hamburger {
  display: block;
  width: 25px;
  height: 3px;
  background-color: white;
  position: relative;
}

.hamburger::before,
.hamburger::after {
  content: "";
  display: block;
  width: 25px;
  height: 3px;
  background-color: white;
  position: absolute;
}

.hamburger::before {
  top: -8px;
}

.hamburger::after {
  bottom: -8px;
}

.hero-element {
  display: flex;
  align-items: center;
  min-height: 380px;
  width: 100%;
  height: 270px;
  object-fit: cover;
  object-position: center;
  text-align: center;
  background-position: center;
  background-color: Black;
}

.hero {
  margin: 0 auto;
  max-width: 800px;
}

.title {
  color: #fff;
  font-weight: 600;
  font-size: 36px;
  text-shadow: 1px 1px #141414;
}

main {
  width: 100%;
  margin: 0 auto;
}

.content {
  padding: 32px;
}

.latest {
  width: 100%;
  margin-top: 30px;
  margin-bottom: 50px;
  text-align: center;
}

.latest h1 {
  font-weight: 700;
  font-size: 2em;
  margin-bottom: 50px;
}

footer {
  background-color: #333;
  color: white;
  font-size: 14px;
  padding: 2em;
  width: 100%;
  text-align: center;
}

.explore h1 {
  font-weight: 700;
  font-size: 2em;
  margin-bottom: 50px;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: green;
  color: white;
  padding: 8px;
  z-index: 100;
}

.skip-link:focus {
  top: 0px;
}

@media (max-width: 768px) {
  .nav {
    flex-direction: column;
    align-items: flex-start;
  }

  .navlist {
    flex-direction: column;
    width: 100%;
  }

  .navitems {
    text-align: center;
  }

  .nav-toggle {
    display: block;
    margin: 1rem;
  }
}
.list {
  margin: 32px auto auto;
  text-align: left;
  display: grid;
  grid-row-gap: 16px;
}
.list-katalog {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
}
.kota {
  position: absolute;
  top: 8px;
  left: 16px;
  border-radius: 5px;
  background-color: green;
  padding: 5px 10px 5px;
  color: white;
  font-weight: 500;
}
.item-content {
  padding: 16px;
}
.gambar {
  width: 100%;
  max-height: 200px;
  display: inline-block;
}
.rating {
  font-size: 12px;
  text-transform: uppercase;
  color: #999;
  font-weight: 500;
  display: inline-block;
}
.rating-value {
  color: gold;
  text-decoration: none;
  display: inline-block;
  min-width: 44px;
  min-width: 44px;
}
.judul {
  font-weight: 500;
  font-size: 16px;
  margin-top: 10px;
  transition: 0.3s opacity;
}
.judul:hover {
  opacity: 0.5;
}
.judul a {
  text-decoration: none;
  color: inherit;
  min-width: 44px;
  min-height: 44px;
}
.deskripsi {
  margin-top: -30px;
  font-size: 12px;
  line-height: 1.5em;
}

input {
  min-width: 44px;
  min-height: 44px;
}

button {
  min-width: 44px;
  min-height: 44px;
}

.navbar {
  display: flex;
  flex-direction: row;
}

.navbar a {
  padding: 30px;
  text-decoration: none;
  color: #4F6F52;
}

.navbar a:hover,
.dropdown:hover .dropbtn {
  background-color: #739072;
  color: #fff;
}

.menu {
  display: none;
  font-size: 32px;
  padding: 20px;
  background-color: #fff;
  outline: none;
  border: none;
}

.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 90%;
  margin: auto;
