___CSS_LOADER_EXPORT___.push([module.id, `.detail {
  background-color: #080808;
  font-family: 'Poppins', sans-serif;
}

.main {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 2rem;
}

.detail-image {
  margin: 2rem;
  width: 100%;
  height: 600px;
  background-size: cover;
  background-position: center;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.detail-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s;
}

.detail-image:hover img {
  transform: scale(1.1);
}

.detail-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.detail-text h1 {
  color: #fff;
  font-size: 40px;
  text-transform: capitalize;
  justify-content: center;
  margin-bottom: 5px;
}

.detail-text h2 {
  color: #fff;
  font-size: 20px;
  text-transform: capitalize;
  letter-spacing: 1px;
  margin-bottom: 15px;
}

h2{
  color: #fff;
}

.description {
  color: #fff;
  font-size: 16px;
  text-align: justify;
  margin-bottom: 15px;
  letter-spacing: 0.5px;
  line-height: 1.6;
}

.menus {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-around;
  width: 80%;
  color: #fff;
  margin-bottom: 30px;
}

.review-item {
  display: flex;
  flex-direction: column;
  border: 1px solid #fff;
  margin-bottom: 1rem;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.review-item p {
  margin: 0.6rem;
}

.review-item-user {
  font-weight: 500;
  color: #fff;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: rgb(0, 0, 0);
  cursor: pointer;
}

.review-item-text {
  font-weight: 300;
  color: #fff;
}

.review-item-date {
  font-weight: 400;
  color: #ECE3CE;
  font-size: 12px;
}
